
import NextParticle from './nextparticle.js'

import {useEffect, useRef,useState, useCallback} from 'react';

export default function Particle(props) {
  const imgRef = useRef(null);
  useEffect(() => {
    let nextParticle = new NextParticle(imgRef.current);
    return () => {
      console.log('end');
      nextParticle.stop();
    }
  }, [])

  return <div>
            <img
              ref={imgRef}
              data-particle-gap="2"
              // data-disable-interaction="true"
              data-gravity="0.1"
              data-noise="1"
              src={props.src}
              data-init-position="top"
              data-init-direction="top"
              data-fade-position="bottom"
              data-fade-direction="bottom"
              data-responsive-width="true"
              // data-width="600"
              // data-height="500"
              // // data-color="#fff"
              // height='500'
              style={{opacity:'0'}}
              // {...props}
            />
    </div>  
}