import { ProGallery } from 'pro-gallery';
import 'pro-gallery/dist/statics/main.css';
import {useLayoutEffect,useState,useRef} from 'react';

export default function Gallery(props) {
  const wrapper = useRef(null)
  // Add your images here...
  const items = props.items;
  const [container, setContainer] = useState({
        width: window.innerWidth,
        height: window.innerHeight
      })
  // The options of the gallery (from the playground current state)
  const options = {
    galleryLayout: -1,
    scrollAnimation: 'FADE_IN',
    imageHoverAnimation: 'ZOOM_IN',
  };
  function resize(params) {
        setContainer({
        width: wrapper.current.clientWidth,
        height:  wrapper.current.clientHeight
      });
    }
  // The size of the gallery container. The images will fit themselves in it
  useLayoutEffect(() => {
    resize();
    window.addEventListener("resize", resize);
    return () => {
      window.removeEventListener("resize", resize);
    }
  }, [])

  // The eventsListener will notify you anytime something has happened in the gallery.
  const eventsListener = (eventName, eventData) => {
    if(eventName==='ITEM_CLICKED') {
      props.onClick&&props.onClick(eventData);
    }
  }; 

  // The scrollingElement is usually the window, if you are scrolling inside another element, suplly it here
  const scrollingElement = window;

  return (
    <div ref={wrapper}>
      <ProGallery
        items={items}
        options={options}
        container={container}
        eventsListener={eventsListener}
        scrollingElement={scrollingElement}
        customHoverRenderer={itemProps => <div>
        <h1 style={{position:'absolute', left:'50%', top:'50%', transform:'translate(-50%,-50%)', textAlign:'center', color:'white'}}>{itemProps.title}<br/> <br/>{itemProps.description}</h1>
        </div>}
        customImageRenderer={imageProps => <img {...imageProps} width="100%" />}
        // customInfoRenderer={itemProps => <div>Info #{itemProps.description}</div>}
      />
    </div>
    
  );
}