import React, { Component } from "react";
import Zmage from "react-zmage";
import { Drawer, Button, Radio, Space } from 'antd';
import Gallery from './components/Gallery';
import LogoDesign from './components/LogoDesign';
import TreeProject from './components/TreeProject';

let id = 0;
class Portfolio extends Component {
  state = { visible: false, selectedProject:{}};

  onClose = () => {
    this.setState({
      visible: false,
    });
  };
  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };
  render() {
    if (!this.props.data) return null;

    const projects = this.props.data.projects.map((projects)=> {
      let projectImage = "/images/portfolio/" + projects.image;

      return { // Image item:
          itemId: projects.title,
          mediaUrl: projectImage,
          metaData: {
            type: 'image',
            height: 101,
            width: 204,
            title: projects.title,
            description: projects.description,
            focalPoint: [0, 0],
            link: {
              url: projectImage,
              target: '_blank',
            },
          }
        }
    });
    const Content = ({type})=>{
      if(type === 'Logo Design'){
        return <LogoDesign></LogoDesign>
      }
      return <TreeProject></TreeProject>
    }
    return (
      <section id="portfolio">
        <div className="row">
          <div className="twelve columns collapsed">
            <h1>Check Out Some of My Works.</h1>
            
            <Gallery
              items={projects}
              onClick={(selectedProject)=>{this.setState({selectedProject});this.showDrawer()}}
            ></Gallery>
            <Drawer
              title={this.state.selectedProject.title}
              placement='bottom'
              closable={true}
              onClose={this.onClose}
              visible={this.state.visible}
              key={'bottom'}
              contentWrapperStyle={{height:'100%'}}
            >
              {this.state.visible&&<Content type={this.state.selectedProject.title}></Content>  }
            </Drawer>
            <h1 style={{padding: '20px'}}>more showcases are underconstructing</h1>
          </div>
        </div>
      </section>
    );
  }
}

export default Portfolio;
